@import url(https://fonts.googleapis.com/css2?family=Bebas+Neue&family=Caveat&family=Lobster&family=Ultra&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  display: flex;
  /* background-color: black; */
  height: 1080px;
}

nav{
    background-color: black;  
    text-decoration: none;
    padding: .3rem;
    
  }
  li{
    list-style: none;
    display: inline;
    padding: 1rem;
    color:white;
    font-size: 1.3rem;
    text-decoration: none;
  }
  
  a{
    font-size: 2rem;
    text-decoration: none;
    color:white;
  }
.wavy {
    position: relative;
    display:inline;
    margin-left:0.5em;
    margin-right:0.5em;
}

h1{
    font-family: 'Caveat', cursive;
}
.wavy span {
    
    position: relative;
    display: inline-block;
    color: #C79871;
    font-size: 2em;
    text-transform: uppercase;
    font-family: 'Bebas Neue', cursive;
    -webkit-animation: animate 2s ease-in-out infinite;
            animation: animate 2s ease-in-out infinite;
}

#first{
    -webkit-animation-delay: 0.1s;
            animation-delay: 0.1s;
}
#second{
    -webkit-animation-delay: 0.2s;
            animation-delay: 0.2s;
}
#third{
    -webkit-animation-delay: 0.3s;
            animation-delay: 0.3s;
}
#fourth{
    -webkit-animation-delay: 0.4s;
            animation-delay: 0.4s;
}


@-webkit-keyframes animate {
    0% {
        -webkit-transform: translateY(0px);
                transform: translateY(0px);
    }

    20% {
        -webkit-transform: translateY(-20px);
                transform: translateY(-20px);
    }

    40%,
    100% {
        -webkit-transform: translateY(0px);
                transform: translateY(0px);
    }
}


@keyframes animate {
    0% {
        -webkit-transform: translateY(0px);
                transform: translateY(0px);
    }

    20% {
        -webkit-transform: translateY(-20px);
                transform: translateY(-20px);
    }

    40%,
    100% {
        -webkit-transform: translateY(0px);
                transform: translateY(0px);
    }
}

/* geeks for geeks code*/
.colorChange{
    font-family: 'Lobster', cursive;
    font-size: 80px;
    -webkit-animation: colorChange 2s ease-in-out infinite alternate;
            animation: colorChange 2s ease-in-out infinite alternate;
    -webkit-animation-duration: infinite;
            animation-duration: infinite;
    display: inline;
    margin-left: 0.5em;
    margin-right:0.5em;
}

@-webkit-keyframes colorChange {
    from {color: lightblue;}
    to {color: #ff00a6;}
}

@keyframes colorChange {
    from {color: lightblue;}
    to {color: #ff00a6;}
}

.glow {
    font-size: 80px;
    color: #000;
    text-align: center;
    -webkit-animation: glow 1s ease-in-out infinite alternate;
            animation: glow 1s ease-in-out infinite alternate;
    display: inline;
    margin-left:0.5em;
    margin-right:0.5em;
  }
  
  @-webkit-keyframes glow {
    from {
      text-shadow: 0 0 10px #fff, 0 0 20px #fff, 0 0 30px rgb(251, 251, 41), 0 0 40px rgb(251, 251, 41), 0 0 50px rgb(251, 251, 41), 0 0 60px rgb(251, 251, 41), 0 0 70px rgb(251, 251, 41);
    }
    
    to {
      text-shadow: 0 0 20px #fff, 0 0 30px rgb(255, 255, 122), 0 0 40px rgb(255, 255, 122), 0 0 50px rgb(255, 255, 122), 0 0 60px rgb(255, 255, 122), 0 0 70px rgb(255, 255, 122), 0 0 80px rgb(255, 255, 122);
    }
  }
  
  @keyframes glow {
    from {
      text-shadow: 0 0 10px #fff, 0 0 20px #fff, 0 0 30px rgb(251, 251, 41), 0 0 40px rgb(251, 251, 41), 0 0 50px rgb(251, 251, 41), 0 0 60px rgb(251, 251, 41), 0 0 70px rgb(251, 251, 41);
    }
    
    to {
      text-shadow: 0 0 20px #fff, 0 0 30px rgb(255, 255, 122), 0 0 40px rgb(255, 255, 122), 0 0 50px rgb(255, 255, 122), 0 0 60px rgb(255, 255, 122), 0 0 70px rgb(255, 255, 122), 0 0 80px rgb(255, 255, 122);
    }
  }

